.pokemon-card {
  background-color: white;
  border-radius: 10px;
  padding: 0px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  margin: 5px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.pokemon-card img {
  max-width: 150px;
  margin-bottom: -20px;
}

.pokemon-card h2 {
  font-size: small;
}
